.video-container {
    position: relative;
    width: 100%;
    max-width: 640px; /* عرض ویدیو */
    margin: 0 auto;
  }
  
  .video-player {
    width: 100%;
    display: block;
  }
  
  .video-cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    cursor: pointer;
    z-index: 2;
  }
  