/* کلیات استایل چت */
.chat-container1 {
    display: flex;
    max-width : 700px;
    margin: auto;
    flex-direction: column;
    height: 100vh; /* ارتفاع کل صفحه */
    background-color: #f0f0f5;
    font-family: Arial, sans-serif;
  }
  .userpicture-chat{
    width: 100px;
    height: 100px;
    background-color: red;
    position: absolute;
    /* right: 100px; */
    /* top: 20px; */

  }
.userinfootherporfile {
    display: flex; /* برای چینش افقی آیتم‌ها */
    align-items: center; /* تراز آیتم‌ها در محور عمودی */
    justify-content: space-between; /* فاصله بین عکس/نام و دکمه */
    padding: 10px;
    background-color: #5a67d8;
    color: white;
    font-size: 1.5rem;
    font-weight: bold;
}
.userinfootherporfile img{
  cursor: pointer;

}
.userinfootherporfile h4{
  margin-right: 10px;
  cursor: pointer;
}
.backchat{
  /* margin-left: -25px !important; */
  margin-top: -13px !important;
  /* background-color: red; */
  width: 30px;
  font-size: 30px;
}

.user-profile-image {
    border-radius: 50%; /* اگر می‌خواهید تصویر گرد باشد */
    margin-right: 10px; /* فاصله بین تصویر و متن */
}

.user-profile-name {
    margin: 0;
}

.userinfootherporfile li {
    margin: 0;
    list-style: none;
}


 
  .tabs1 > div{
    max-width: 700px;
  }
  /* پیام‌ها در ناحیه‌ای اسکرولی نمایش داده می‌شوند */
  .chat-messages {
    flex-grow: 1;
    overflow-y: auto;
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    transition: all 0.3s ease;
    animation: fadeIn 0.5s ease-in-out;
    max-height: calc(100vh - 140px); /* تنظیم ارتفاع برای نمایش اسکرول */
  }
  
  .chat-message {
    padding: 8px 12px;
    border-radius: 8px;
    max-width: 70%;
    font-size: 1rem;
    line-height: 1.4;
    transition: all 0.3s ease;
  }
  
/* پیام‌های ارسالی به سمت راست چسبانده می‌شوند */
.chat-message-sent {
    align-self: flex-end;
    background-color: #3182ce;
    color: white;
    margin-left: auto; /* پیام‌های ارسال‌شده سمت راست */
}

/* پیام‌های دریافتی به سمت چپ چسبانده می‌شوند */
.chat-message-received {
    align-self: flex-start;
    background-color: #e2e8f0;
    color: black;
    margin-right: auto; /* پیام‌های دریافت‌شده سمت چپ */
}

  
  /* انیمیشن برای ظاهر شدن پیام‌ها */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  /* استایل بخش ورودی پیام */
  .chat-input-container {
    display: flex;
    padding: 10px;
    max-width : 600px;
    background-color: #fff;
    border-top: 1px solid #e2e8f0;
    position: fixed;
    bottom: 45px;
    left: 0;
    border-radius: 20px;
    right: 0; /* برای قرارگیری وسط صفحه */
    width: 90%; /* پهنا کمتر از کل صفحه */
    margin: 0 auto; /* وسط چین کردن */
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.1);
  }
    
  .chat-input {
    flex-grow: 1;
    border: 1px solid #cbd5e0;
    border-radius: 4px;
    outline: none;
    font-size: 1rem;
    margin-right: 5px;
    height: 20px;
    transition: all 0.3s ease;
  }
  
  .chat-input:focus {
    border-color: #3182ce;
  }
  
  .chat-send-button {
    background-color: #3182ce;
    color: white;
    border: none;
    height: 42px;
    margin-top: 10px;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .chat-send-button:hover {
    background-color: #2b6cb0;
  }
  