/* SearchExplore.module.css */
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  
  .title {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .searchBox {
    padding: 10px;
    width: 80%;
    max-width: 400px;
    border-radius: 8px;
    border: 1px solid #ddd;
    outline: none;
    margin-bottom: 20px;
    font-size: 16px;
  }
  
  .resultsContainer {
    width: 80%;
    max-width: 400px;
  }
  
  .resultItem {
    padding: 10px;
    border-bottom: 1px solid #eee;
    font-size: 18px;
    cursor: pointer;
    transition: background-color 0.2s;
  }
  
  .resultItem:hover {
    background-color: #f0f0f0;
  }
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
  }
  
  .searchBox {
    width: 90%;
    padding: 8px;
    margin: 10px 0;
    font-size: 16px;
    border-radius: 5px;
    border: 1px solid #ddd;
  }
  
  .tabsExplore {
    /* display: flex; */
    /* justify-content: space-around; */
    width: 100%;
    /* padding: 10px 0; */
    border-top: 1px solid #ddd;
    background-color: #fff;
    position: fixed;
    bottom: 0;
  }
  
  .postGrid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2px;
    margin-top: 10px;
    width: 100%;
  }
  .loadingexplore{
    width: 100%;
  }
  .postItem {
    position: relative;
    overflow: hidden;
  }
  
  .postMedia {
    width: 100%;
    height: 100%;
    object-fit: cover;
    max-height: 300px;
  }
  
  @media screen and (min-width : 900px) {
    .postGrid {
      grid-template-columns: repeat(4, 1fr);

    }
    
  }